import axios from "axios";

/**
 * 
 *
 * @export
 * @param {*} searchName
 * @returns
 */
export function noticeList(language) {
  return axios.get(`/api/v1/ucsp/portal/notice/unreads/${language}`);
}
/**
 * portal
 *
 * @export
 * @param {*} searchName
 * @returns
 */
export function resetNoticeList() {
  return axios.put(`/api/v1/ucsp/portal/notice/read`);
}
